@mixin mobile{
    @media screen and (max-width:480px) {
        @content;
    }
}
@mixin tablet{
    @media screen and (max-width:768px) {
        @content;
    }
}
@mixin laptop{
    @media screen and (max-width:1200px) {
        @content;
    }
}






.formContainer{
    background-color: #BFACE2;
    background-image: url("../public/bac.jpeg");
    height: 100vh;
    display:flex;
    align-items: center;
    justify-content: center;
    
    
    .formWrapper{
        background-color:white ;
        padding: 20px 60px;
        border-radius: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        .logo{
            color: #A084DC;
            font-weight: bold;
            font-size: 24px;
        }
        .title{
            color:#BFACE2;
            font-size: 18px;
        }
        form{
            display: flex;
            flex-direction: column;
            gap: 15px;
            input{
                padding: 15px;
                border: none;
                width: 250px;
                border-bottom:1px solid #BFACE2;
                &::placeholder{
                    color: #BFACE2;
                }
            }
            button{
                background-color: #BFACE2;
                color: white;
                padding: 10px;
                font-weight: bold;
                border: none;
                border-radius: 20px;
                 cursor: pointer;
                &:hover{
                    background-color: #534c9d;
                    color: white;
                }

            }
            label{
                display: flex;
                align-items: center;
                color: #BFACE2;
                cursor: pointer;
                font-size: 15px;
                &:hover{
                    color: #534c9d;
                }
                
                img{
                    width:40px;
                    height: auto;
                    padding-right: 10px;
                }
            }
        }
        p{
            color: #A084DC;
            font-size: 12px;
            margin-top: 10px;
            cursor: pointer;
            &:hover{
                color: #534c9d;
            }

        }
    }
}


.home{
    background-color:#BFACE2;
    background-image: url("../public/bac.jpeg");
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    .container{
        border: 1px solid white;
        height: 100%;
        width:100%;
        display: flex;
        overflow: hidden;
        
        .Sidebar{
            flex: 1;
            background-color: #A084DC;
            position: relative;
            .Navbar{
                display: flex;
                align-items: center;
                background-color: #645CBB;
                height: 50px;
                padding: 10px;
                justify-content: space-between;
                color: white;
                .logo{
                    font-weight: bold;
                    @include tablet{
                        display: none;
                    }
                }
                .user{
                    display: flex;
                    gap: 10px;
                }
                img{
                    background-color: #BFACE2;
                    height: 24px;
                    width: 24px;
                    border-radius: 50%;
                    object-fit: cover;
                }
                button{
                    background-color:#BFACE2;
                    border: none;
                    border-radius: 10%;
                    color: #645CBB;
                    &:hover{
                        background-color: #534c9d;
                        color: white;
                    }
                    @include tablet{
                        position: absolute;
                        bottom: 10px;
                    }
                }
                
            }
            .Search{
                border-bottom: 1px solid grey;
                .SearchForm{
                    padding: 10px;
                    input, button{
                        background-color: transparent;
                        color: white;
                        border-color: white;
                        border-top:2px solid white;
                        border-left:2px solid white;
                        border-bottom:2px solid white;
                        border-right:2px solid white;
                        border-radius: 20px;
                        outline: none;
                        padding: 10px;
                        width: 80%;
                        margin-bottom: 2px;
                        &::placeholder{
                            color: white;
                        }
                    }
                    button{
                        &:hover{
                            background-color: white;
                            color: #534c9d;
                            cursor: pointer;
                        }
                    }
                }
                
            }
            .UserChat{
                padding: 5px;
                display: flex;
                align-items: center;
                gap: 10px;
                color: white;
                cursor: pointer;
                img{
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    object-fit: cover;
                }
                &:hover{
                    background-color: #534c9d;
                }
                .UserChatInfo{
                    span{
                        font-size: 18px;
                        font-weight: 500;
                    }
                    p{
                        margin: 0;
                        font-size: 14;
                        color: lightgray;
                    }
                }
            }

            
        }
        .Chat{
            flex: 2;
            .ChatInfo{
                height: 50px;
                background-color: #534c9d;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 10px;
                color: lightgray;
            }
            .ChatIcons{
                display: flex;
                gap: 10px;
                img{
                    height: 24px;
                    cursor: pointer;

                }
            }
            .Messages{
                background-color: transparent;
                padding: 10px;
                height: calc(100% - 160px);
                margin-bottom: 20px;
                overflow: scroll;
                overflow-x: hidden;
                .Message{
                    display: flex;
                    gap: 20px;
                    .MessageInfo{
                        display: flex;
                        flex-direction: column;
                        color: white;
                        font-weight: 300;
                        img{
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        object-fit: cover;
                        }
                        
                    }
                    .MessageContent{
                        max-width: 80%;
                        display: flex;
                        flex-direction: column;
                        gap: 10px;
                        p{
                            background-color: white;
                            padding: 10px 20px;
                            border-radius:0px 20px 20px 20px;
                            max-width: max-content;
                        }
                        img{
                            width: 50%;
                        }
                    }
                    &.Owner{
                        flex-direction: row-reverse;
                        .MessageContent{
                            align-items: flex-end;
                        }
                        p{
                            background-color: #534c9d;
                            color: white;
                            border-radius: 20px 0 20px 20px;
                        }
                    }
                }
            }
            .Input{
                background-color: #BFACE2;
                height: 50px;
                padding: 10px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                input{
                    width: 100%;
                    border: none;
                    margin-bottom: 20px;
                    outline: none;
                    font-size: 18px;
                    color: #534c9d;
                    background-color:#BFACE2;
                    &::placeholder{
                        color: lightgray;
                    }
                }
                .Send{
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    img{
                        height: 24px;
                        cursor: pointer;
                    }
                    button{
                        border: none;
                        padding: 10px 15px;
                        color: #BFACE2;
                        background-color: #534c9d;
                        cursor: pointer;
                        border-radius: 20px;
                        &:hover{
                            color: #534c9d;
                            background-color: white;
                        }
                    }
                }


            }
        }
    }
}